//////// MOBILE
.awards-page-wrapper-mobile {
  position: relative;
  display: flex;
  width: 100%;
  align-items: center;

  .awards-listing {
    position: relative;
    width: 100%;
    animation: 1s slideFromBottomListingRow;

    a {
      position: absolute;
      height: 100%;
      width: 100%;
      z-index: 1;
    }
    
    .title-and-key {
      position: relative;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      margin: 10px 15px 10px 35px;
    }
    
    .awards-listing-title {
      font-weight: 100;
      font-size: 24px;
    }

    .awarded-restaurant {
      cursor: pointer;

      &:nth-child(2) { border-top: 1px solid $grey-light; }

      .restaurant {
        position: relative;
        display: flex;
        background: #fff;
        box-shadow: 5px 5px 15px rgba(214, 31, 31, 0.05);
        justify-content: space-between;
        width: calc(100% - 40px);
        margin: 20px;
        height: 220px;
        cursor: pointer;
      }
    }
  }
}
