/////// MAP
.google-map {
  position: relative;
  width: 55%;
  height: 100%;

  .gm-style .gm-style-iw-c {
    padding: 0 !important;
  }
  
  .gm-style .gm-style-iw-t:after {
    background: linear-gradient(45deg,#000, 50%,rgba(255,255,255,0) 51%,rgba(255,255,255,0) 100%);
  }
  
  .gm-style .gm-style-iw-d {
    overflow: hidden !important;
  }
  
  .gm-ui-hover-effect {
    margin: 5px !important;
  }
  
  .gm-svpc, div[title="Afficher les images satellite"], div[title="Afficher un plan de ville"] {
    display: none !important;
  }
  
  ////// MARKERS WINDOW
  .marker-info-window {
    max-height: 140px;
    font-family: "Montserrat";
    max-width: 200px;
    cursor: pointer;
    background-color: #000;
    
    img {
      max-height: 140px;
      opacity: 0.6;
    }
  }
  
  .marker-header {
    position: absolute;
    display: flex;
    align-items: center;
    top: 15px;
    left: 15px;
    font-weight: 700;
    color: #fff;
  
    .marker-rating {
      font-size: 14px;
    }
  
    .fa-star {
      margin-right: 1px;
      font-size: 12px;
    }
  }
  
  .marker-restaurant-name {
    position: absolute;
    color: #fff;
    bottom: 35px;
    left: 15px;
    font-size: 18px;
    font-weight: 900;
  }
  
  .marker-restaurant-type {
    position: absolute;
    color: #fff;
    bottom: 15px;
    left: 15px;
    font-size: 14px;
    font-weight: 700;
  }
}
